// Mixin for adaptive images

@mixin adaptive-bg($image, $ext: "jpg") {
  background-image: image-set(
    url("#{$image}@2x.webp") 2x,
    url("#{$image}.webp") 1x
  );

  &--no-webp {
    background-image: url("#{$image}.#{$ext}");

    @include retina() {
      background-image: url("#{$image}@2x.#{$ext}");
    }
  }
}
