@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 100 900;
  font-display: swap;
  src:
    url("../fonts/montserrat-cyrillic-variable-wghtOnly-normal.woff2") format("woff2"),
    url("../fonts/montserrat-cyrillic-variable-wghtOnly-normal.woff") format("woff");
  unicode-range:
    U+0301,
    U+0400-045F,
    U+0490-0491,
    U+04B0-04B1,
    U+2116;
}

